ol, li {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
  vertical-align: baseline;
}

.list-ordered {
  margin: 0rem 0rem 2.25rem 3rem;
  padding-left: 2.6875rem;
}

.list-ordered li {
  line-height: 1.5rem;
  margin-bottom: 0.375rem;
  padding-left: 0.3125rem;
  position: relative;
}
